.nvbb {
  margin-right: 0;
  padding-right: 0;
}
.icon-bar {
  background-color: #fff;
}
.nvbb {
  position: relative;
  float: right;
  padding: 9px 10px;
  margin-top: 8px;
  margin-right: 15px;
  margin-bottom: 8px;
  background-color: transparent;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
  z-index: 10000;
}
.nvbb .icon-bar {
  width: 22px;
  transition: all 0.2s;
  -webkit-transition: all 0.2s;
}
.nvbb .top-bar {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform-origin: 10% 10%;
  -webkit-transform-origin: 10% 10%;
  -ms-transform-origin: 10% 10%;
}
.nvbb .bottom-bar {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform-origin: 10% 90%;
  -webkit-transform-origin: 10% 90%;
  -ms-transform-origin: 10% 90%;
}
.nvbb .middle-bar {
  opacity: 0;
}
.nvbb.collapsed .top-bar,
.nvbb.collapsed .bottom-bar {
  transform: rotate(0);
  -webkit-transform: rotate(0);
  -ms-transform: rotate(0);
}
.nvbb.collapsed .middle-bar {
  opacity: 1;
}
.nvbb .icon-bar {
  display: block;
  width: 22px;
  height: 2px;
  border-radius: 1px;
  margin-top: 4px;
}
